.lessonContent_fillTable {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin: 3vh 0 0 0;
    width: 76vw;
}

.lessonContent_fillTable_img {
  margin-top: -80px;
  pointer-events: none;
}

.lessonContent_tableForFill {
    display: flex;
   flex-direction: column;

}

.lessonContent_tableForFill p{
margin: 0;
}

.lessonContent_CardsForDrop {
    display: flex;
    flex-direction: row;
}

.lessonContent_CardsForDrag {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}


.lessonContent_CardsForDrop_card {
    box-sizing: border-box;
    padding: 15px;
    font-size: 20px;
    width: 215px;
    letter-spacing: 2px;
    min-height: 250px;
    height: fit-content;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 
    margin: 10px;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 
    background: linear-gradient(to top, white 78%, transparent 65%);

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}

.lessonContent_CardsForDrag_card {
    user-select: none;
    margin: 5px;
    background-color: #ffffff;
    border: none;
    color: #434553;
    width: 180px;
    height: 65px; 
    padding: 10px  10px;
    cursor: grab;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 
    box-sizing: border-box;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    color: #434553;

    position: relative;
}

.lessonContent_CardsForDrag_card:hover {
    box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce; 

}

.lessonContent_CardsForDrag_card p {
  
    pointer-events: none;
    text-align: center;
    margin: 0;

}

@media screen and (max-width: 1370px) {
    .lessonContent_fillTable_img img {
        width: 325px;
        margin-top: 105px;
        pointer-events: none;
      }
      .lessonContent_CardsForDrop_card {
          width:183px;
          margin: 6px;
      }

      .lessonContent_fillTable { 
        margin: 0 0 0 0;
    }
}