.lessonContent_sentenceWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   margin: 20px 0 0 0;
   width: 80vw;
}


.lessonContent_sentenceHeading {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid rgb(199, 199, 199);
    height: 60px;
}

.lessonContent_sentenceExamples {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    height: 50vh;
}

.nonTargetSentenceColor {
    box-sizing: border-box;
    padding: 16px;
    color: rgba(67, 69, 83, 1);
    cursor: pointer;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
}

.lessonContent_sentenceHeading p:nth-child(-n+2) {
    margin: 0 1.9vw 0 0;
}

.targetSentenceColor {
    box-sizing: border-box;
    padding: 16px;
    cursor: pointer;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    color: #1D66FF;
    border-bottom: 3px solid #1D66FF;
}



.sentenceExample_examples {  
    margin: 10px;
    padding: 12px;

    color: #434553;
    min-width: 400px;
    height: 4.5vh;
    display: flex;
    align-items: center;
    /* cursor: pointer; */
    text-align: left;

    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    background-color: #ffffffe7;
    border: none;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px;      
}

.boldStyle {
    font-weight: 600;
}

@media screen and (max-width: 1370px) {
    .lessonContent_sentenceWrapper {
        margin: 0px;

    }
    .lessonContent_sentenceExamples {
        height: 55vh;
    }
    .sentenceExample_examples {
        margin: 6px;
        padding:8px;
        min-width: 400px;
        height: 4.5vh;
    }
    .lessonContent_sentenceExamples {
        height: 45vh;
    }
}