.lessonsContent_alphabetCapitalSmall {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 63vw;
}

.letterCard_capital {
    user-select: none;
    margin: 6px;
    background-color: #ffffff;
    border: 0.5px solid #1D66FF;
    color: #434553;
    min-width: 65px;
    min-height: 65px; 
    padding: 2px 2px;
    font-weight: 600;
    font-style: normal;
    font-size: 20px;
/* 
    cursor: pointer; */


    
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 

    display: flex;
    flex-direction: row;
    /* justify-content: space-between; */
    align-items: flex-end;
}

.letterCard_capital p {
 margin: 0 0 12px 12px;

}

.capitalLetters {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 8vh 0;
}

.smallLetters {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}


.smallLetters {
    max-width: 55vw;
}

.letterCard_small {
    user-select: none;
    margin: 6px;
    background-color: #ffffff;
    border: none;
    color: #434553;
    min-width: 50px;
    min-height: 50px;
    box-sizing: border-box;
    padding: 15px 0 0 0;
    font-weight: 600;
    font-style: normal;
    font-size: 17px;

    cursor: grab;
    text-align: center;


    
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 

  
}

.letterCard_small:hover {
    box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce; 
}

.letterCard_small p {
 margin: 0 0 0 0;

}

.pointerEventNone {
    pointer-events: none;
}

.localReset {
    color: white;
    font-size:15px;
    position: absolute;
    margin: 10px 0 50px 54px;
    cursor: pointer;
}

@media screen and (max-width: 1370px) {
    .capitalLetters {
        margin: 25px 0;
    }

    .lessonsContent_alphabetCapitalSmall {
        width: 80vw;
    }
    .smallLetters {
        max-width: 60vw;
    }
    
}