.lessonContent_picturesForMatching {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: inherit;
    height: inherit;
  
}

.picturesForMatching_pictures {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 801px;
}

.picturesForMatching_pictureCard {
    user-select: none;
    margin: 10px;
    background-color: #ffffff;
    border: none;
    color: #434553;
    min-width: 240px;
    min-height: 190px; 
    padding: 2px 2px;
    position: relative;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

   
}

.picturesForMatching_pictureCard img {
pointer-events: none;
position: absolute;
}

.picturesForMatching_words {
    margin: 5vh 0 0 0; 
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 80vw;
}

.picturesForMatching_wordCard {
    user-select: none;
    margin: 6px;
    background-color: #ffffff;
    border: none;
    color: #434553;
    min-width: 100px;
    min-height: 50px; 
    padding: 2px 2px;
    cursor: grab;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 
    letter-spacing: 2px;
    text-align: center;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 44px;
    color: #434553;

}

.picturesForMatching_wordCard:hover {
    box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce; 
}

.picturesForMatching_wordCard p{
    pointer-events: none;
     margin: 0;
    padding: 0;
}    

.picturesForMatching_wordCard_active {
    margin: 120px 0 0 120px;
    background-color: #5a8efd;
    color: white;
    box-shadow: none;
}

.picturesForMatching_wordCard_active:hover {
    box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce; 
 
  
}

@media screen and (max-width: 1370px) {
    .picturesForMatching_pictureCard {
        margin: 10px;
        min-width: 185px;
        min-height: 141px; 
        padding: 0; 
    }

    .picturesForMatching_pictureCard img {
        width:90%
        }

        .picturesForMatching_wordCard_active {
            margin: 72px 0 0 65px;
        }
}