.lessonContent_alphabetIcons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.alphabetIcons {
    margin: 20px 0 0 30px;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: center;
    align-content: flex-start;
    width: 80vw;
    padding: 0 0 0 0;
    height: 60vh;;
}


.letterIconCard {
    user-select: none;
    margin: 8px;
    background-color: #ffffff;
    border: none;
    color: #434553;
    min-width: 130px;
    min-height: 130px; 
    padding: 2px 2px;
    /* font-weight: 600;
    font-style: normal;
    font-size: 24px; */

    cursor: pointer;

    text-align: center;
    
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

   
}

.letterIconCard:hover {
    box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce; 

   
}

.LetterForIcons {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    font-weight: 600;
    font-style: normal;
    font-size: 24px;
}

.WordForIcons {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    font-size: 21px;
}

.letterIconCard img{
  width: 120px;
  height: 120px;
}

@media screen and (max-width: 1370px) {
    .letterIconCard {
        margin: 5px;
        min-width: 80px;
        min-height:80px; 
        padding: 0 0;   
    }

    .letterIconCard img{
        width: 110px;
        height: 110px;
        
      }
}