.lessonContent_rusEngWords38 {
  width: 70vw;
  height: 65vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  align-content: center;
}

.lessonContent_rusEngWords38 p {
  margin: 30px 0 30px 20px;
  font-size: 24px;
}

.rusEngWords38_charts {
  margin: 15px;
  padding: 20px 40px;
  color: #434553;
  width: 20vw;
  height: 3vh;
  cursor: pointer;
  text-align: left;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  background-color: #ffffffe7;
  border: 2px solid transparent;
  box-shadow: -5px -5px 15px #ffffff, 5px 5px 15px #e9ecf4;
  border-radius: 8px;
}

.active_t38 {
  border: 2px solid blue;
}
@media screen and (max-width: 1370px) {
  .adptive_longWords {
    padding: 15px 40px;
    height: 3vh;
    width: 24vw;
  }
}
