.canvasWrapper {
  margin: 2vh 0;
  width: 70%;
  height: 70%;
  position: relative;
  border: 3px dashed rgb(151, 144, 247);
  border-radius: 70px;
  cursor: url(img/markerBlue.png), auto;
}

.panelSettings {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 65%;
}

.brushSettings {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  margin: 10px 0 0 0;
}

.brushSize {
  width: 100%;
  margin: 0 0 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

.brushColor {
  width: 70%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.canvasClearButton {
  background: transparent;
  color: #1d66ff;
  width: 300px;
  height: 70px;
  border: 1px solid #1d66ff;
  box-sizing: border-box;
  border-radius: 12px;
  padding: 15px 32px;
  font-size: 21px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.canvasClearButton:hover {
  background: rgba(96, 146, 255, 0.24);
}

.circleRed,
.circleGreen,
.circleBlue {
  outline: 3px dashed rgb(151, 144, 247);
  border-radius: 50%;
  height: 3em;
  width: 3em;
  cursor: pointer;
  transition: all 0.3s ease;
}

.circleRed {
  background: red;
}

.circleGreen {
  background: green;
}

.circleBlue {
  background: #1d66ff;
}

.cicleActive {
  outline: 6px dashed rgb(151, 144, 247);
  transition: all 0.3s ease;
}
