.wrapperTask3 {
    width: 70vw;
    height: 55vh;
    /* background-color: rgb(228, 190, 190); */

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
    align-content: center;
    /* flex-flow: column wrap; */

}



.chartsTask3 {
    cursor: pointer;
    margin: 10px;
    padding: 12px;

    color: #434553;
    width: 28vw;
   max-height: 6vh;
    min-height: 5vh;
   

    /* cursor: pointer; */
 

    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    background-color: #ffffffe7;
    border: none;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 


    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.chartsTask3:hover {
    box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce; 
}

@media screen and (max-width: 1370px) {
    .wrapperTask3 {
        height: 60vh;
    }
    .chartsTask3 {
        padding: 9px;
        min-height: 6vh;
    }
}

