.oneChoisePictureWrapper {
  margin: 30px 0 0 0;
  width: 80%;
  height: 100%;
  justify-content: center;
  align-content: flex-start;
  align-items: flex-start;
}

.picturesForMatchingPictures {
  user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.picturesForMatchingCard {
  user-select: none;
  margin: 10px;
  background-color: #ffffff;
  border: none;
  padding: 2px 2px;
  position: relative;
  box-shadow: -5px -5px 15px #ffffff, 5px 5px 15px #e9ecf4;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.picturesForMatchingCard img {
  pointer-events: none;
  width: 10vw;
  border-radius: 12px;
}

.picturesForMatchingCard:hover {
  box-shadow: -5px -5px 15px #b8b7b7, 5px 5px 15px #99999b;
  cursor: url(img/legCursorMin.png), auto;
}

.picturesForMatchingCard:active {
  box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce;
  cursor: url(img/legCursorActive.png), auto;
}

.picturesForMatchingCardActive {
  box-shadow: none;
  outline: 5px solid #ff0080;
}
