.wrapperTask5 {
    width: 70vw;
    height: 65vh;
    /* background-color: rgb(228, 190, 190); */

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap;
    align-content: center;
    /* flex-flow: column wrap; */

}

.chartsTask5 {
    
    margin: 15px;
 
    padding: 20px 40px;

    color: #434553;
  width: 15vw;
  height: 3vh;

    cursor: pointer;
    text-align: left;

    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    background-color: #ffffffe7;
    border: 2px solid transparent;

    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px;    
}

.activet5 {
    border: 2px solid blue;
}

@media screen and (max-width: 1370px) {
    .chartsTask5 {
        padding: 15px 40px;
        height: 2vh;  
    }
}