.lessonContent_startContent {
    width: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
}



.textStartContent {
    width: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}


.textStartContent_text {
    display: flex;
    flex-wrap: nowrap;
    place-content: stretch flex-end;
    align-items: center;
    width: 50vw;
    height: 50vh;
    overflow: hidden auto;
}

.textStartContent_button {
    width: 50px;
    height: 50px;
    background-color: #DDE7FD  ;
    border-radius: 25px;
    position: relative;
    transition: all 0.2s;
    cursor: pointer;
}

.leftButton {
    user-select: none;
   margin: 0 5vw 0 0 ;
}

.rightButton {
    user-select: none;
    margin: 0 0 0 5vw;
 }

.textStartContent_button:hover {
    background-color: #dde7fda1;
} 

.textStartContent_textNoWCounter {
    margin: 1% 0 0 0;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 18px;
    color: #000000;
}

.textStartContent_textNoWCounter__actual {
    font-weight: 600;
    font-size: 23px;
    color: #000000;
}


.flyingBat_leftSideWrapper {
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 14vh;
    left: 8vw;
}

.flyingBat_rightSideWrapper {
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 20vh;
    left: 76vw;
    
}

.pumpkin_rightSideWrapper {
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 60vh;
    left: 83vw;
    animation: bounce_rightPumpkin 3s infinite;

}

@keyframes bounce_rightPumpkin {
    50% { 
        transform: translateY(50px);
    }
    100% { 
        transform: translateY(0px);
    }
}

.pumpkin_leftSideWrapper {
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 60vh;
    left: 7vw;
    animation: bounce_leftPumpkin 3.5s infinite;

}


@keyframes bounce_leftPumpkin {
    50% { 
        transform: translateY(50px);
    }
    100% { 
        transform: translateY(0px);
    }
}


/*  SVG ANIMATON */

#eyes_flyingBat_leftSide {
    animation: eyes_leftSideBat 2s ease;
    animation-iteration-count: infinite;
    animation-delay: 0;
}

@keyframes eyes_leftSideBat {
    0% { 
        opacity:1;
    }
    50% { 
        opacity: 0;
    }
}


#bodyWings_flyingBat_leftSide {
    animation: bodyWings_leftSideBat 1s ease;
    animation-iteration-count: infinite;
    transform-origin: center;
}

@keyframes bodyWings_leftSideBat {
    0% { 
        transform: rotate(0deg);
    }
    50% { 
        transform:  rotate(8deg);;
    }
}











#eyes_flyingBat_rightSide {
    animation: eyes_rightSideBat 2s ease;
    animation-iteration-count: infinite;
    animation-delay: 0;
}

@keyframes eyes_rightSideBat {
    0% { 
        opacity:1;
    }
    50% { 
        opacity: 0;
    }
}


#bodyWings_flyingBat_rightSide {
    animation: bodyWings_rightSideBat 2s ease;
    animation-iteration-count: infinite;
    transform-origin: center;
}

@keyframes bodyWings_rightSideBat {
    0% { 
        transform: rotate(0deg);
    }
    50% { 
        transform:  rotate(5deg);;
    }
}