.voiceRecordBtn {
    background: transparent;
    margin: 20px 0 0 0; 
    color: #1D66FF;
    width: 520px;
    height: 70px;
    border: 1px solid #1D66FF;
    box-sizing: border-box;
    border-radius: 12px;
    padding: 15px 32px;
    font-size: 21px;
    font-weight: bold;
    cursor: pointer;
 

    display: flex;
    justify-content: space-between;
    align-items: center;
   
}