.lessonContent_alphabetDND {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.alphabetDNDLesson {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 80vw;
    padding: 0 0 0 0;
    margin: 20px 0;
    /* height: 60vh;; */
}


.letterCard_drag {
    user-select: none;
    margin: 10px;
    background-color: #ffffff;
    border: none;
    color: #434553;
    min-width: 50px;
    min-height: 50px;
  
    padding: 20px 20px;
   
    font-weight: 600;
    font-style: normal;
    font-size: 24px;

    cursor: grab;

    text-align: center;
    
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px; 
   
}

.letterCard_drag:hover {
    box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce; 

}

.letterCard_drag_active:hover {
    background-color: #5a8efdc4;

}

.letterCard_drag_active {
    box-sizing: border-box;
    user-select: none;
    /* margin: 10px; */
    background-color: #5a8efd;
    border: none;
    color: #FFFF;
    /* min-width: 50px;
    min-height: 50px;
  
    padding: 21px 21px; */

    min-width: inherit;
    min-height: inherit;
    padding: 8px 0 0 0;
    
   
    font-weight: 600;
    font-style: normal;
    font-size: 24px;

    cursor: grab;

    text-align: center;
    
    /* box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4; */
    border-radius: 8px; 
   
}

.letterCard_drag p, .letterCard_drag_active p {
    pointer-events: none;
    padding-top: 25%;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    margin: 0;
      
}

.letterCard_drop {
    border: 0.5px solid #1D66FF;
    user-select: none;
    margin: 10px;
    background-color: #ffffff;
    min-width: 92px;
    min-height: 92px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 9px; 
   
}


@media screen and (max-width: 1370px) {
  
    .letterCard_drop {
        margin: 5px;
        min-width: 71px;
        min-height: 71px;
    }
    .letterCard_drag {
        margin: 5px;
        min-width: 40px;
        min-height: 40px;
        padding: 15px 15px;
        font-size: 25px;
    }
    .letterCard_drag p{
        font-size: 22px;
    }
    .letterCard_drag_active {
        font-size: 22px;
        padding: 5px 0 0 0;
    }
}