.lessonContent_listenAndChoosePicture {
    margin: 15px 0 0 0;
    height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}


.lessonContent_player {
/* border: 1px solid rgb(177, 173, 173); */
width: 27vw;
height: 7vh;
border-radius: 12px; 
display: flex;
justify-content: center;
align-items: center;
/* background-color: #fff; */
}
.lessonContent_player audio {
    width: inherit;
    /* box-shadow: -5px -5px 15px #ffffff, 5px 5px 15px #e9ecf4; */
}

audio::-webkit-media-controls-panel {
    background-color: #DDE7FD;
}

.lessonContent_picturesForChoose {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.player_picturesForChoose_img {
    cursor: pointer;
}

.player_picturesForChoose_img:hover {
    opacity: 0.7;
}

.checkIcon1, .checkIcon2, .checkIcon3  {
    position: absolute;
}

.checkIcon1 {
left: 30vw;
}

.checkIcon2 {
    left: 55vw;

}

.checkIcon3 {
left: 80vw;
    
}