.lessonContent_rusEngWords39 {
    width: 70vw;
    height: 65vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap;
    align-content: center;
}


 .lessonContent_rusEngWords39 p{
    margin: 30px 0 30px 20px;
    font-size:24px;
 }


 .rusEngWords39_charts {
    margin: 15px;
    padding: 20px 40px;
    color: #434553;
    width: 20vw;
    height: 3vh;
    cursor: pointer;
    text-align: left;
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    background-color: #ffffffe7;
    border: 2px solid transparent;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px;   
}


.active_t39 {
    border: 2px solid blue;
}