.lessonContent_chooseArticle_sentence {
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    color: #434553;
}

/* .lessonContent_chooseArticle {
    margin: 3vh 0 0 0;
} */

.lessonContent_select {
    text-align: center;
    font-size: 40px;
    width: fit-content;
    border: none;
    box-shadow: -5px -5px 15px #FFFFFF, 5px 5px 15px #E9ECF4;
    border-radius: 8px;

}

.lessonContent_select::-ms-expand {
    display: none;
} 
.lessonContent_select:hover {
    cursor: pointer;
    border-color: #888;
} 
.lessonContent_select:focus {

    border-radius: 8px;
    border-color: #aaa; 
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
    box-shadow: 0 0 0 3px -moz-mac-focusring; 
    color: #222;
    outline: none; 
} 

.lessonContent_select option {

    border-radius: 5px;
  
} 

@media screen and (max-width: 1370px) {
    .lessonContent_chooseArticle p{
        margin: 3vh 0 0 0;
        font-size: 30px;
    }
}