.alphabetModal {
    height: 100vh;
    width: 100vw;
    backdrop-filter: blur(2px);
    position: fixed;
    top: 190px;
    left: 725px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    transition: all 0.2s ease;
    z-index: 999;

}

.alphabetModal.active {
    transform: scale(1);
    top: 0;
    left: 0;
    transition: all 0.2s ease;
}


.alphabetModal__content {
    /* padding: 40px; */
    border-radius: 12px;
    background-color: #F3F6FF;
    height: 67vh;
    width: 67vw;
    box-sizing: border-box; 
}


.alphabetStyleParrotWrapper {
    position: absolute;
    top: 67vh;
    left: 93vw;
    cursor: pointer;
}

.alphabetStyleParrot {
    width: 90%;
    height: 90%;
    transition: all 0.2s ease;
}


.alphabetStyleParrot:hover {
    width: 100%;
    height: 100%;
    transition: all 0.2s ease;
}

@media screen and (max-width: 1370px) {
    .alphabetStyleParrotWrapper {
        top: 69vh;
        left: 93vw;
    }
    .alphabetStyleParrot {
        width: 80%;
        height: 80%;
    }
    .alphabetStyleParrot:hover {
        width: 90%;
        height: 90%;
       
    }
}